<script>
//	import VuePaginate from 'vue-paginate'
//	import {asf,init,nextPage} from '../../assets/js/pages.js'
	import {getService} from '../../assets/js/configs.js'
	export default {
	  name: 'orderList',
//	  components: {
//	    VuePaginate
//	  },
	  data () {
	    return {
	      orderListInfo:'',
	      dealState:'',
	      currentPage:1,
	      totalPage:1,
	      pageCount:10
	    }
	  },
	  mounted:function (){
	  	this.getListInfo(this.currentPage);
	  },
	  methods:{
	  	getListInfo:function (num) {
	  		$.ajax({
	  			type:'post',
	  			url:getService()+"/bill/getBills",
	  			dataType: "json",
	  			data:{
	  				dealState:this.dealState,
	  				page:parseInt(num)-1,
	  				unit:this.pageCount
	  			},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 0) {
			    		this.orderListInfo = res.data;
			    		for (var i=0;i<this.orderListInfo.length;i++) {
			    			this.orderListInfo[i].dealDate = this.transformDate(this.orderListInfo[i].dealDate);
			    		}
			    		var totalNum = parseInt(res.nofdata);
			    		var pageMore = totalNum % this.pageCount;
			    		if (pageMore == 0) {
			    			this.totalPage = parseInt(totalNum / this.pageCount);
			    		} else{
			    			this.totalPage = parseInt(totalNum / this.pageCount) + 1;
			    		}
			    		console.log(this.orderListInfo);
			    	}
			    	
			    }.bind(this)
	  		});
	  	},
	  	currentPageInfo:function (pageNum) {
	  		this.currentPage = pageNum;
	  		this.getListInfo(this.currentPage);
	  	},
//	  	时间戳转为日期
	  	transformDate:function (timeStamp) {
	  		var date = new Date(timeStamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
	        var y = date.getFullYear();    
		    var m = date.getMonth() + 1;    
		    m = m < 10 ? ('0' + m) : m;    
		    var d = date.getDate();    
		    d = d < 10 ? ('0' + d) : d;    
		    var h = date.getHours();  
		    h = h < 10 ? ('0' + h) : h;  
		    var minute = date.getMinutes();  
		    var second = date.getSeconds();  
		    minute = minute < 10 ? ('0' + minute) : minute;    
		    second = second < 10 ? ('0' + second) : second;   
		    return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;  
//	        var str = Y+M+D+h+m+s;
//	        return date;
	  	}
	  }
	}
</script>

<style>
	@import url("../../assets/css/PCstyle.css");
