		            	<div class="row" style="margin-top: 15px;">
		            		<div class="col-lg-12 col-md-12" style="padding: 0 15px;">
		            			<button type="button" class="pull-right btn btn-warning" id="nextStepBtn" style="display: none;" v-on:click="nextStep()">下一步</button>
		            			<button type="button" class="btn btn-warning pull-right" id="saveAndSendBtn" style="display: none;" v-on:click="submitInfo()">送出并确认</button>
		            		</div>
		            	</div>
		            </div>
		        </div><!-- /.modal-content -->
		    </div><!-- /.modal -->
		</div>
	</div>
</template>

<script>
	import {getService} from '../../assets/js/configs.js'
//	import {checkLogin} from '../../assets/js/checkLogin.js'
	import appeal from './appeal.vue'
	import notice from './notice.vue'
	export default {
	  name: 'cen',
	  data(){
	  	return{
	  		userInfo:'',
	  		bankNum:'',
	  		zhifubaoNum:'',
	  		wechatNum:'',
	  		type:'bank',
	  		typeList:[],
	  		accountName:'',
	  		sixCode:'',
	  		checkCode:'',
	  		secretPhone:'',
	  		bankName:'',
	  		notices:[],
	  		noticeTitle:'',
	  		noticeTime:'',
	  		noticeContent:'',
	  		personalType:'document', //设置个人资料模态框的当前功能（新增推荐人，修改个人资料等）
	  		referer:'',
	  		realIDNumber:'', //身份证号
	  		realName:'', //真实姓名
	  		lockPhone:'',
	  		timer:60,
	  		timeInterval:'',
	  		timeNewInterval:'',
	  		timeEmailInterval:'',
	  		changePhoneCode:'',
	  		newPhone:'',
	  		newPhoneCode:'',
	  		newEmail:'',
	  		currentPassword:'',
	  		newPassword:'',
	  		newPasswordCheck:'',
	  		emailCode:'',
	  		nickName:'',
	  		changeEmailCode:'',
	  		centerNoticeTitle:'会员公告',
	  		getCenterNotice:{
	  			showFlag:false,
	  			noticeArray:[]
	  		},
	  		
	  	}
	  },
	  components:{
			appeal,
			notice
		},
	  mounted:function (){
	  	this.getInfo();
	  	this.getTypes();
//	  	this.getNotices();
//	  	checkLogin();
		this.getCenterNoticeFunc();

	  },
	  methods:{
	  	getCenterNoticeFunc(){
	  		var that=this;
	  		var param = {
		  		"location":'会员中心'
		  	}
	  		$.ajax({
		  			type:'post',
		  			url:getService()+"/announcement/findCommonAnnouncement",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	console.log('res=',res);
				    	if(res.error_code == 0){
				    		that.getCenterNotice.showFlag=true;
				    		that.getCenterNotice.noticeArray = res.data;
				    	}
				    	
				    },
				    error:function(res){
				    	
				    }
		  		});

	  	},
	  	getInfo:function () {
	  		$.ajax({
	  			type:'get',
	  			url:getService()+"/user/getInfo",
	  			dataType: "json",
	  			data:{
	  				
	  			},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 0) {
			    		this.userInfo = res.data;
			    		this.secretPhone = this.setSecretPhone(this.userInfo.tel_number);
			    		this.bankNum = this.userInfo.bankAccounts.length;
			    		this.zhifubaoNum = this.userInfo.aliPayAccounts.length;
			    		this.wechatNum = this.userInfo.wechatAccounts.length;
			    		this.lockPhone = this.userInfo.tel_number.substr(0,2)+'****'+this.userInfo.tel_number.substr(6,4);
			    	}
			    }.bind(this)
	  		});
	  	},
	  	getTypes:function () {
	  		$.ajax({
	  			type:'get',
	  			url:getService()+"/dujiuxing",
	  			dataType: "json",
	  			data:{
	  				
	  			},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	if (res.error_code == 0) {
			    		this.typeList = res.data;
			    		this.typeOptions = this.typeList.bank;
			    	}
			    }.bind(this)
	  		});
	  	},
	  	showAppealModal:function () {
	  		$('#appealModal').modal('show');
	  	},
	  	showBankModal:function () {
	  		$('#bankModal').modal('show');
	  	},
	  	setSecretPhone:function (num) {
	  		var starPhone = num.substr(0,3)+'***'+num.substr(7);
	  		return starPhone;
	  	},
	  	submitAccount:function () {
	  		var typeStr = '';
	  		switch (this.type){
	  			case 'bank':
	  				typeStr = '銀行';
	  				break;
	  			case 'postOffice':
	  				typeStr = '郵局';
	  				break;
	  			case 'cooperative':
	  				typeStr = '信用合作社';
	  				break;
	  			case 'association':
	  				typeStr = '農會';
	  				break;
	  			case 'fishing':
	  				typeStr = '漁會';
	  				break;
	  			default:
	  				break;
	  		}
	  		var param = {
	  			bankType:typeStr,
	  			bankName:this.bankName,
	  			accountName:this.accountName,
	  			last6digital:this.sixCode,
	  			accountTelNumber:this.userInfo.tel_number
	  		}
	  		$.ajax({
	  			type:'post',
	  			url:getService()+"/addbank",
	  			dataType: "json",
	  			data:JSON.stringify(param),
	  			async:true,
				headers: {'Content-Type': 'application/json'},
	  			xhrFields: {
			        withCredentials: true
			    },
			    crossDomain: true,
			    success:function (res) {
			    	console.log(res);
			    }.bind(this)
	  		});
	  	},
	  	showPersonalModal:function () {
	  		$('#personalModal').modal('show');
	  	},
	  	submitInfo:function () {
	  		var _this = this;
	  		switch (this.personalType){
	  			case 'referee':
	  				if (this.referer != '') {
	  					var param = {
		  					referer:this.referer
		  				}
	  					$.ajax({
				  			type:'post',
				  			url:getService()+"/setReferer",
				  			dataType: "json",
				  			data:JSON.stringify(param),
				  			async:true,
							headers: {'Content-Type': 'application/json'},
				  			xhrFields: {
						        withCredentials: true
						    },
						    crossDomain: true,
						    success:function (res) {
						    	if (res.error_code == 0) {
						    		$('#personalModal').modal('hide');
						    		alert('推荐成功');
						    	} else{
						    		alert(res.error_massage);
						    	}
						    }.bind(this)
				  		});
	  				} else{
	  					alert('请填写推荐人手机号或ID');
	  				}
	  				break;
	  			case 'validate':
	  				if (this.realIDNumber != '' && this.realName != '') {
	  					var reg = /^[a-zA-Z][0-9]{9}$/;
	  					if (reg.test(this.realIDNumber)) {
	  						var param = {
			  					realIDNumber:this.realIDNumber,
			  					realName:this.realName
			  				}
		  					$.ajax({
					  			type:'post',
					  			url:getService()+"/addUserRealName",
					  			dataType: "json",
					  			data:JSON.stringify(param),
					  			async:true,
								headers: {'Content-Type': 'application/json'},
					  			xhrFields: {
							        withCredentials: true
							    },
							    crossDomain: true,
							    success:function (res) {
							    	if (res.error_code == 200) {
							    		alert('验证成功')
							    	} else{
							    		alert(res.error_massage);
							    	}
							    }.bind(this)
					  		});
	  					} else{
	  						alert('请输入有效的身份证');
	  					}
	  				} else{
	  					alert('信息不能为空');
	  				}
	  				break;
	  			case 'phone':
	  				$.ajax({
			  			type:'post',
			  			url:getService()+"/user/updatePhoneNumber",
			  			dataType: "json",
			  			data:{
			  				tel_number:this.newPhone,
			  				code:this.newPhoneCode
			  			},
			  			xhrFields: {
					        withCredentials: true
					    },
					    crossDomain: true,
					    success:function (res) {
					    	if (res.error_code == 0) {
					    		alert('更新成功，请重新登录');
					    		$('#personalModal').modal('hide');
					    		_this.$router.push({path:'/login'});
					    	} else{
					    		alert(res.error_msg);
					    	}
					    }
			  		});
	  				break;
	  			case 'password':
	  				if (this.newPassword != this.newPasswordCheck) {
	  					alert('两次密码输入不一致');
	  				} else{
	  					$.ajax({
				  			type:'post',
				  			url:getService()+"/user/updatePassword",
				  			dataType: "json",
				  			data:{
				  				currentPassword:this.currentPassword,
				  				newPassword:this.newPassword
				  			},
				  			xhrFields: {
						        withCredentials: true
						    },
						    crossDomain: true,
						    success:function (res) {
						    	if (res.error_code == 200) {
						    		alert('更新成功，请重新登录');
						    		$('#personalModal').modal('hide');
						    		_this.$router.push({path:'/login'});
						    	} else{
						    		alert(res.error_massage);
						    	}
						    }
				  		});
	  				}
	  				break;
	  			case 'email':
	  				if (this.newEmail == '') {
	  					alert('请填写新邮箱');
	  				} else{
	  					$.ajax({
				  			type:'post',
				  			url:getService()+"/mail/change_Email",
				  			dataType: "json",
				  			data:{
				  				code:this.emailCode,
				  				email_address:this.newEmail
				  			},
				  			xhrFields: {
						        withCredentials: true
						    },
						    crossDomain: true,
						    success:function (res) {
						    	if (res.error_code == 0) {
						    		alert('邮箱更新成功');
						    		$('#personalModal').modal('hide');
						    	} else{
						    		alert(res.error_msg);
						    	}
						    }
				  		});
	  				}
	  				break;
	  			case 'nickName':
	  				if (this.nickName == '') {
	  					alert('请填写昵称');
	  				} else{
	  					$.ajax({
				  			type:'post',
				  			url:getService()+"/user/updateNickname",
				  			dataType: "json",
				  			data:{
				  				nickName:this.nickName
				  			},
				  			xhrFields: {
						        withCredentials: true
						    },
						    crossDomain: true,
						    success:function (res) {
						    	if (res.error_code == 200) {
						    		alert('昵称更新成功');
						    		$('#personalModal').modal('hide');
						    	} else{
						    		alert(res.error_msg);
						    	}
						    }
				  		});
	  				}
	  				break;
	  			default:
	  				break;
	  		}
	  	},
	  	setPersonalType:function (str) {
	  		this.personalType = str;
	  		switch (str){
	  			case 'referee':
	  				$('#validateRow').hide();
	  				$('#phoneRow').hide();
	  				$('#documentRow').hide();
	  				$('#emailRow').hide();
	  				$('#passwordRow').hide();
	  				$('#nickNameRow').hide();
	  				$('#refereeRow').show();
	  				$('#nextStepBtn').hide();
	  				$('#saveAndSendBtn').show();
	  				break;
	  			case 'validate':
	  				$('#refereeRow').hide();
	  				$('#phoneRow').hide();
	  				$('#documentRow').hide();
	  				$('#emailRow').hide();
	  				$('#passwordRow').hide();
	  				$('#nickNameRow').hide();
	  				$('#validateRow').show();
	  				$('#nextStepBtn').hide();
	  				$('#saveAndSendBtn').show();
	  				break;
	  			case 'phone':
	  				$('#refereeRow').hide();
	  				$('#validateRow').hide();
	  				$('#documentRow').hide();
	  				$('#emailRow').hide();
	  				$('#passwordRow').hide();
	  				$('#nickNameRow').hide();
	  				$('#phoneRow').show();
	  				$('#saveAndSendBtn').hide();
	  				$('#nextStepBtn').show();
	  				break;
	  			case 'document':
	  				$('#refereeRow').hide();
	  				$('#validateRow').hide();
	  				$('#phoneRow').hide();
	  				$('#emailRow').hide();
	  				$('#passwordRow').hide();
	  				$('#nickNameRow').hide();
	  				$('#documentRow').show();
	  				$('#saveAndSendBtn').hide();
	  				$('#nextStepBtn').hide();
	  				break;
	  			case 'email':
	  				$('#refereeRow').hide();
	  				$('#validateRow').hide();
	  				$('#phoneRow').hide();
	  				$('#documentRow').hide();
	  				$('#passwordRow').hide();
	  				$('#nickNameRow').hide();
	  				$('#emailRow').show();
	  				$('#nextStepBtn').hide();
	  				$('#saveAndSendBtn').show();
	  				break;
	  			case 'password':
	  				$('#refereeRow').hide();
	  				$('#validateRow').hide();
	  				$('#phoneRow').hide();
	  				$('#documentRow').hide();
	  				$('#emailRow').hide();
	  				$('#nickNameRow').hide();
	  				$('#passwordRow').show();
	  				$('#nextStepBtn').hide();
	  				$('#saveAndSendBtn').show();
	  				break;
	  			case 'nickName':
	  				$('#refereeRow').hide();
	  				$('#validateRow').hide();
	  				$('#phoneRow').hide();
	  				$('#documentRow').hide();
	  				$('#emailRow').hide();
	  				$('#passwordRow').hide();
	  				$('#nickNameRow').show();
	  				$('#nextStepBtn').hide();
	  				$('#saveAndSendBtn').show();
	  				break;
	  			default:
	  				break;
	  		}
	  	},
	  	sendCode:function () {
//	  		if (this.registPhone == '') {
//	  			$('#message').html('手機號不能為空');
//	  			$('#myModal').modal('show');
//	  		}
//	  		else if(!this.registPhoneConfig.test(this.registPhone)){
//	  			$('#message').html('請輸入正確的手機號');
//	  			$('#myModal').modal('show');
//	  		}
//	  		else{
	  			$('#sendCodeBtn').attr('disabled','disabled');
	  			$('#sendCodeBtn').css('background','#ccc');
	  			this.timeInterval = setInterval(this.setTimer,1000);
	  			$.ajax({
		  			type:'get',
		  			url:getService()+"/msg/currentSendMassage",
		  			dataType: "json",
//		  			data:{
//		  				tel_number:this.userInfo.tel_number
//		  			},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	console.log(res);
				    }
		  		});
//	  		}
	  		
	  	},
	  	setTimer:function () {
	  		$('#sendCodeBtn').html(this.timer+'秒');
	  		this.timer = parseInt(this.timer) - 1;
	  		if (this.timer == -1) {
	  			clearInterval(this.timeInterval);
	  			this.timer = 60;
	  			$('#sendCodeBtn').removeAttr('disabled');
	  			$('#sendCodeBtn').css('background','#00a6ff');
	  			$('#sendCodeBtn').html('發送驗證碼');
	  		}
	  	},
	  	setNewTimer:function () {
	  		$('#sendNewCodeBtn').html(this.timer+'秒');
	  		this.timer = parseInt(this.timer) - 1;
	  		if (this.timer == -1) {
	  			clearInterval(this.timeNewInterval);
	  			this.timer = 60;
	  			$('#sendNewCodeBtn').removeAttr('disabled');
	  			$('#sendNewCodeBtn').css('background','#00a6ff');
	  			$('#sendNewCodeBtn').html('發送驗證碼');
	  		}
	  	},
	  	setEmailTimer:function () {
	  		$('#sendEmailCodeBtn').html(this.timer+'秒');
	  		this.timer = parseInt(this.timer) - 1;
	  		if (this.timer == -1) {
	  			clearInterval(this.timeEmailInterval);
	  			this.timer = 60;
	  			$('#sendEmailCodeBtn').removeAttr('disabled');
	  			$('#sendEmailCodeBtn').css('background','#00a6ff');
	  			$('#sendEmailCodeBtn').html('發送驗證碼');
	  		}
	  	},
	  	nextStep:function () {
	  		if(this.changePhoneCode == ''){
	  			alert('请输入验证码')
	  		}
	  		else{
	  			$.ajax({
		  			type:'post',
		  			url:getService()+"/user/verifySendMassage",
		  			dataType: "json",
		  			data:{
		  				code:this.changePhoneCode
		  			},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == 0) {
				    		$('#phoneSet1').hide();
				  			$('#phoneSet2').show();
				  			$('#nextStepBtn').hide();
				  			$('#saveAndSendBtn').show();
				    	} else{
				    		alert(res.error_msg);
				    	}
				    }
		  		});
	  		}
	  	},
	  	sendNewCode:function () {
	  		if(this.newPhone == ''){
	  			alert('请输入手机号');
	  		}
	  		else{
	  			$('#sendNewCodeBtn').attr('disabled','disabled');
	  			$('#sendNewCodeBtn').css('background','#ccc');
	  			this.timeNewInterval = setInterval(this.setNewTimer,1000);
	  			$.ajax({
		  			type:'post',
		  			url:getService()+"/msg/updatePhoneNumberSendMassage",
		  			dataType: "json",
		  			data:{
		  				tel_number:this.newPhone
		  			},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == 0) {
				    		console.log(res);
				    	} else{
				    		alert(res.error_msg);
				    	}
				    }
		  		});
	  		}
	  	},
	  	sendEmailCode:function () {
	  		if(this.newEmail == ''){
	  			alert('请输入邮箱');
	  		}
	  		else{
	  			$('#sendEmailCodeBtn').attr('disabled','disabled');
	  			$('#sendEmailCodeBtn').css('background','#ccc');
	  			this.timeEmailInterval = setInterval(this.setEmailTimer,1000);
	  			$.ajax({
		  			type:'post',
		  			url:getService()+"/mail/send_mail",
		  			dataType: "json",
		  			data:{
		  				email_address:this.newEmail
		  			},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == 0) {
				    		console.log(res);
				    	} else{
				    		alert(res.error_msg);
				    	}
				    }
		  		});
	  		}
	  	}
	  }
	}
</script>

<style>
	@import url("../../assets/css/PCstyle.css");
</style>


